<template>
  <div class="foot-container">
    <div class="van-doc-card" v-if="showImg">
      <van-swipe :autoplay="4000" lazy-render ref="mySwiper">
        <van-swipe-item v-for="item in images" :key="item.id">
          <img
            :ref="'img' + item.id"
            :src="item.url"
            :download_url="item.download_url"
            width="100%"
            height="500px"
          />
        </van-swipe-item>
      </van-swipe>
      <div style="margin: 16px;">
        <van-button
          round
          block
          type="primary"
          native-type="submit"
          @click="download"
        >
          下载正在展示的图片
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Foot',
  data() {
    return {
      imgIndex: 0
    }
  },
  props: {
    showImg: {
      type: Boolean,
      default: true
    },
    images: {
      type: Array,
      default: []
    }
  },
  methods: {
    download() {
      console.log(this.$refs.mySwiper.active)
      let down = document.createElement('a')
      down.href = this.images[this.$refs.mySwiper.active].url
      down.download = 'image' + new Date().getTime() + '.webp'
      down.click()
    }
  }
}
</script>

<style lang="less" scoped>
.van-doc-card {
  margin: 24px 16px 0 16px;
  padding: 8px 24px 24px 24px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 8px 12px #ebedf0;
}
.van-swipe {
  border-radius: 30px;
}
</style>
