<template>
  <div class="showvideo-container">
    <div class="van-doc-card" v-if="begin">
      <video
        controls="controls"
        autoplay="autoplay"
        name="media"
        :src="videoUrl"
        style="width: 100%; margin-top: 14px;"
      ></video>
      <div style="margin: 16px;">
        <van-button
          round
          block
          type="primary"
          native-type="submit"
          @click="download"
        >
          下载
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShowVideo',
  props: {
    videoUrl: {
      type: String,
      default: ''
    },
    begin: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  methods: {
    download() {
      let down = document.createElement('a')
      down.href = this.videoUrl
      down.download = 'video' + new Date().getTime() + '.mp4'
      down.click()
    }
  }
}
</script>

<style lang="less" scoped>
.van-doc-card {
  margin: 16px 16px 0 16px;
  padding: 8px 24px 24px 24px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 8px 12px #ebedf0;
  #title {
    text-align: center;
    font-size: 20px;
  }
}
</style>
