import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import ImageParser from '@/views/ImageParser.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/image',
    name: 'ImageParser',
    component: ImageParser
  }
]

const router = new VueRouter({
  routes
})

export default router
