<template>
  <div class="card-container">
    <div class="van-doc-card">
      <h3 id="title" tabindex="-1">{{ title }}</h3>
      <van-form>
        <van-cell-group inset>
          <van-field
            v-model="shareText"
            name="抖音分享链接"
            label="分享链接"
            placeholder="抖音分享文本"
            :rules="[{ required: true }]"
          />
        </van-cell-group>
        <div style="margin: 16px;">
          <van-button
            round
            block
            type="primary"
            native-type="submit"
            @click="work"
          >
            提交
          </van-button>
        </div>
        <div style="margin: 16px;">
          <van-button
            round
            block
            type="warning"
            native-type="submit"
            @click="reset"
          >
            重新输入
          </van-button>
        </div>
      </van-form>
    </div>
    <Foot :images="images" :showImg="showImg"></Foot>
  </div>
</template>

<script>
import axios from 'axios'
import Foot from '@/components/image/Foot.vue'
import { Toast } from 'vant'
axios.defaults.headers.post['Content-Type'] =
  'application/x-www-form-urlencoded'

export default {
  name: 'Body',
  data() {
    return {
      shareText: '',
      images: [],
      imgNum: 0,
      showImg: false
    }
  },
  props: ['title'],
  computed: {
    shareLink() {
      let pattern = /http[s]:\/\/([\w-]+\.)+[\w-]+(\/[\w-./?%&=]*)?/
      if (pattern.test(this.shareText) == true) {
        return pattern.exec(this.shareText)[0]
      }
    }
  },
  methods: {
    reset() {
      this.shareText = ''
    },
    async work() {
      const { data: res } = await axios.post('/api/douyin/images', {
        params: {
          url: this.shareLink
        }
      })
      if (!res.item_list) {
        Toast('未知错误请检查输入或者询问站长')
        return
      }
      const imgs = res.item_list[0].images
      this.imgNum = imgs.length
      for (var i = 0; i < this.imgNum; i++) {
        this.images[i] = {
          url: imgs[i].url_list[0],
          id: i
        }
      }
      this.showImg = true
    }
  },
  components: {
    Foot
  }
}
</script>

<style lang="less" scoped>
.van-doc-card {
  margin: 16px 16px 0 16px;
  padding: 8px 24px 24px 24px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 8px 12px #ebedf0;
  #title {
    text-align: center;
    font-size: 20px;
  }
}
</style>
